.skills {
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 20px;

    @media (max-width: 768px) {
        width: 100%;
        align-items: center;
        gap: 10px;
        min-height: 500px;
    }
}

.skills-content {
    padding: 10px;
    box-sizing: border-box;
    transition: 0.5s;
    display: flex;
    width: 95%;
    border-radius: 20px;
    box-shadow: rgba(175, 121, 121, 0.35) 0px 5px 15px;
    margin: 0 auto;

    &:hover {
        transition: 0.5s;
        transform-origin: center;
        transform: scale(1.05);
    }
    @media (max-width: 768px) {
        width: 80%;
        flex-direction: column;
        gap: 1.5rem;

        &:hover {
            transform: scale(1);
        }
    }

    & ul {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 50%;
        margin: 0px;
        list-style-type: none;
        height: 100%;
        box-sizing: border-box;
        gap: 10px;
    
        & h3 {
            position: sticky;
            font-size: 28px;
            font-weight: 600;
            margin: 0px;
        }
    
        & li {
            position: sticky;
            width: 50%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 22px;
    
            & span {
                min-width: 50px;
                min-height: 50px;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    
        @media (max-width: 768px) {
            box-sizing: border-box;
            gap: 0px;
            width: 100%;
            padding: 0;
            height: 50%;
    
            h3 {
                font-size: 18px;
                margin: 0px;
            }
    
            li {
                font-size: 16px;
                width: 80%;
    
                span {
                    font-size: 16px;
                }
            }
        }
    }
    
}

